import { zodResponseFormat } from "openai/helpers/zod";
import { z } from "zod";
import { SearchIntent, StringArray } from "./response-type";
import OpenAI from "openai";
import { Heading } from "../article/heading";

const LLM_MODEL = "gpt-4o-mini";

export class Prompt {

  constructor(
    public client: OpenAI
  ) { }

  async requestString(
    system_msg: string,
    user_msg: string
  ) {
    let completion = await this.client.chat.completions.create({
      model: LLM_MODEL,
      messages: [
        { role: "system", content: system_msg },
        { role: "user", content: user_msg }
      ]
    })
    let res = completion.choices[0].message.content;
    if (res == undefined) {
      throw new Error("Can't parse to json");
    }
    return res;
  }

  async requestStringArray(
    system_msg: string, user_msg: string
  ) {
    let completion = await this.client.beta.chat.completions.parse({
      model: LLM_MODEL,
      messages: [
        { role: "system", content: system_msg },
        { role: "user", content: user_msg }
      ],
      response_format: zodResponseFormat(StringArray, "event"),
    });
    let res = completion.choices[0].message.parsed;
    if (res == undefined) {
      throw new Error("Can't parse to json");
    }
    return res.array;
  }

  async requestObject(
    system_msg: string, user_msg: string,
    format: z.ZodObject<any>
  ) {
    let completion = await this.client.beta.chat.completions.parse({
      model: LLM_MODEL,
      messages: [
        { role: "system", content: system_msg },
        { role: "user", content: user_msg },
      ],
      response_format: zodResponseFormat(format, "event"),
    });
    let res = completion.choices[0].message.parsed;
    if (res == undefined) {
      throw new Error("Can't parse to json");
    }
    return res;
  }

  async suggestTitles(
    keywords: string[],
    target: string
  ) { //TODO typescript
    let system_msg = "SEO10";
    let user_msg = `
      :${keywords.join(", ")} 
      : ${target}`;
    const res = await this.requestStringArray(system_msg, user_msg);
    return res;
  }

  async suggestSeoKeywords(
    keywords: string[],
  ) {
    let system_msg = "SEO30";
    let user_msg = `:${keywords.join(", ")}`;
    const res = await this.requestStringArray(system_msg, user_msg);
    return res;
  }

  async suggestOutlines(
    title: string,
    keywords: string[],
    target: string
  ) {
    let system_msg = "10";
    let user_msg = `
      :${title}
      :${keywords.join(", ")} 
      : ${target}`;
    const res = await this.requestStringArray(system_msg, user_msg);
    return res;
  }

  async suggestSubheadings(
    title: string,
    heading: string
  ) {
    let system_msg = "10";
    let user_msg = `
      :${title}
      :${heading}`;
    const res = await this.requestStringArray(system_msg, user_msg);
    return res;
  }

  async generateBody(
    title: string,
    heading: string,
    subheadings: string[],
    desiredLength: number,
  ) {
    let system_msg = `
      SEO
      

      : 
      ${desiredLength && `: ${desiredLength} `}

      : 
        - 
        - p
        - ##h2###h3
        - strong
    `;
    let sub = subheadings.map(v => `### ${v}`).join("\n");
    let user_msg = `
      :${title}
      :
        ## ${heading}
        ${sub}
    `;
    const res = await this.requestString(system_msg, user_msg);
    return res;
  }

  // 
  async classifySearchIntent(
    heading: string,
    subheadings: string[],
  )
    : Promise<(typeof SearchIntent)[]> {
    const arr = await Promise.all(subheadings.map(async v => {
      let system_msg = "(BuyKnowDoGo)";
      let user_msg = `
        :${heading}
        :${v}
      `;
      const res = await this.requestObject(system_msg, user_msg, SearchIntent)
      return res.type;
    }));
    console.log(arr)
    return arr;
  }

}